    import service from '../../service.js';
    import {
        mapState,
        mapMutations
    } from 'vuex'
    import mInput from '../../components/m-input.vue'

    export default {
        components: {
            mInput
        },
        data() {
            return {
                providerList: [],
                hasProvider: false,
                account: '',
                password: '',
                positionTop: 0
            }
        },
        methods: {
            ...mapMutations([
				'haslogin',
				'saveUserMsgs'
			]),
            initProvider() {
                const filters = ['weixin', 'qq', 'sinaweibo'];
                uni.getProvider({
                    service: 'oauth',
                    success: (res) => {
                        if (res.provider && res.provider.length) {
                            for (let i = 0; i < res.provider.length; i++) {
                                if (~filters.indexOf(res.provider[i])) {
                                    this.providerList.push({
                                        value: res.provider[i],
                                        image: '../../static/img/' + res.provider[i] + '.png'
                                    });
                                }
                            }
                            this.hasProvider = true;
                        }
                    },
                    fail: (err) => {
                        console.error('获取服务供应商失败：' + JSON.stringify(err));
                    }
                });
            },
            initPosition() {
                this.positionTop = uni.getSystemInfoSync().windowHeight - 100;
            },
            bindLogin() {
                if (this.account.length < 5) {
                    uni.showToast({
                        icon: 'none',
                        title: '账号最短为 5 个字符'
                    });
                    return;
                }
                if (this.password.length < 6) {
                    uni.showToast({
                        icon: 'none',
                        title: '密码最短为 6 个字符'
                    });
                    return;
                }
				uni.showLoading({
					title: '登录中',
					mask: true
				});
				this.$http.login({
					number:this.account,
					pass:this.password
				}).then(res => {
					uni.hideLoading()
					console.log("login", res);
					if (res.data == 0) {
						uni.showToast({
							title: '登录失败',
							icon:'none',
							duration:3000
						});
					} else {
						uni.setStorageSync('status', 1)
						uni.setStorageSync('uid', res.data.id)
						this.saveUserMsgs(res.data)
						uni.showToast({
							title: '登录成功',
							icon:'none',
							duration:3000
						});
						uni.switchTab({
							url: '../index/index'
						});
					}
				})
            },
            oauth(value) {
                uni.login({
                    provider: value,
                    success: (res) => {
                        uni.getUserInfo({
                            provider: value,
                            success: (infoRes) => {
                                this.toMain(infoRes.userInfo.nickName);
                            }
                        });
                    },
                    fail: (err) => {
                        console.error('授权登录失败：' + JSON.stringify(err));
                    }
                });
            },
        },
        onReady() {
            this.initPosition();
            this.initProvider();
        }
    }
